import {authProfileUserAPI, securityAPI} from "../DAL/api";

const SET_USER_DATA = "social-network/auth/SET_USER_DATA";
const GET_CAPTCHA_SUCCESS = "social-network/auth/GET_CAPTCHA_SUCCESS";

let initialState = {
    email: null,
    login: null,
    isAuth: false,
    id: null,
    captchaUrl: null,
};


let authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
        case GET_CAPTCHA_SUCCESS:
            return {
                ...state,
                ...action.payload
            };
        default:
            return state;
    }
}


export const setAuthProfileUserData = (id, email, login, isAuth) => ({
    type: SET_USER_DATA,
    payload: {id, email, login, isAuth}
});

export const getCaptchaUrlSuccess = (captchaUrl) => ({
    type: GET_CAPTCHA_SUCCESS,
    payload: {captchaUrl}
});


export const getAuthProfileUser = () => {
    return async (dispatch) => {
        let response = await authProfileUserAPI.me();
        if (response.data.resultCode === 0) {
            // let {id, email, login} = data.data;
            await dispatch(setAuthProfileUserData(response.data.data.id, response.data.data.email, response.data.data.login, true));
        }
    };
}


export const getLoginUser = (email, password, rememberMe, captcha, setStatus) => {
    return async (dispatch) => {
        let response = await authProfileUserAPI.login(email, password, rememberMe, captcha);
        if (response.data.resultCode === 0) {
            dispatch(getAuthProfileUser())
        } else {
            setStatus(response.data.messages)
            if (response.data.resultCode === 10) {
                dispatch(getCaptchaUrl());
            }
        }
    };
};


export const getCaptchaUrl = () => {
    return async (dispatch) => {
        const response = await securityAPI.getCaptchaServer();
        const captchaUrl = response.data.url
        dispatch(getCaptchaUrlSuccess(captchaUrl))
    }
}


export const getLogoutUser = (captchaUrl) => {
    return async (dispatch) => {
        let response = await authProfileUserAPI.logout();
        if (response.data.resultCode === 0) {
            dispatch(setAuthProfileUserData(null, null, null, null))
            dispatch(getCaptchaUrlSuccess(captchaUrl = null))
        }
    };
};


export default authReducer;